import { useMutation, useQueryClient } from "@tanstack/react-query";
import { LeadStatus } from "@everlast/types";
import { updateLeadStatusApi } from "@/lib/mock-api";

const DEFAULT_TENANT = "tenant-demo-1";

export function useUpdateLeadStatus(tenantId: string = DEFAULT_TENANT) {
  const client = useQueryClient();
  return useMutation({
    mutationFn: ({ leadId, status }: { leadId: string; status: LeadStatus }) =>
      updateLeadStatusApi(tenantId, leadId, status),
    onSuccess: (lead) => {
      client.invalidateQueries({ queryKey: ["leads", tenantId] });
      client.invalidateQueries({ queryKey: ["lead", tenantId, lead.id] });
    },
  });
}

