import { useQuery } from "@tanstack/react-query";
import { listTasksByLead } from "@/lib/mock-api";

const DEFAULT_TENANT = "tenant-demo-1";

export function useLeadTasks(leadId: string, tenantId: string = DEFAULT_TENANT) {
  return useQuery({
    queryKey: ["tasks", tenantId, leadId],
    queryFn: () => listTasksByLead(tenantId, leadId),
    enabled: Boolean(tenantId && leadId),
  });
}

