import { useQuery } from "@tanstack/react-query";
import { getLeadById } from "@/lib/mock-api";

const DEFAULT_TENANT = "tenant-demo-1";

export function useLead(leadId?: string, tenantId: string = DEFAULT_TENANT) {
  return useQuery({
    queryKey: ["lead", tenantId, leadId],
    queryFn: () => getLeadById(tenantId, leadId!),
    enabled: Boolean(leadId),
  });
}

